.red{
    color: rgb(220, 43, 20)!important;
}
@media(max-width:8000px){
    .buyNow_container{
        font-size: 14px;
        font-weight: 600;
        color: rgb(134, 134, 134);
        background: white;
    }
    .buyNow_header{
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    .buyNow_header div{
        width: 30%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .buyNow_header_logo img{
        width: 150px;
    }
    .buyNow_header_back_btn{
        color: rgb(32, 124, 230);
        font-size: 13px;
        font-weight: 600;
        cursor: pointer;
        transition: 0.5s ease;  
    }
    .buyNow_header_back_btn:hover{
        color:  rgb(99, 179, 233);;
    }
    .leftArrow{
        font-size: 20px;
        display: flex;
        align-items: center;
        transition: 0.5s ease;
    }
    .buyNow_header_link_div{
        padding-top: 5px;
        font-size: 12px;
        display: flex;
        justify-content: center;
    }
    .buyNow_header_links{
        cursor: pointer; 
        width: 25%!important;  
        transition: 0.5s ease;  
    }
    .buyNow_header_links:hover{
        color: rgb(32, 124, 230);
        transform: translateY(-3px);
    }
    .buyNow_modal{
        display: flex;
        justify-content: space-around;
    }
    .modal_left , .modal_right{
        width: 20%;
    }
    .coursesFrom , .coursesTo{
        width: 100%;
        padding: 15px;
        border: 1px solid #eee;
        border-radius: 10px;
    }
    .coursesFrom ul , .coursesTo ul{
        margin: 0;
        padding: 0;
    }
    .buyNow_card{
        width: 45%;
        border: 0;
        box-shadow: 2px 2px 15px 3px rgb(198, 203, 207);
        border-radius: 10px;
    }
    .buyNow_card_header{
        display: flex;
        justify-content: space-around;
        background: #eee;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
    }
    .buyNow_card_header_left{
        width: 50%;
        display: flex;
        justify-content: center;
    }
    .buyNow_card_header_right{
        width: 50%;
        display: flex;
        justify-content: space-around;
    }
    .buyNow_card_body{
        padding-top: 10px;
        padding-bottom: 5px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
    }
    .buyNow_card_body_img{
        width: 80%;
    
    }
    .buyNow_card_body_left{
        width: 50%;
        display: list-item;
        list-style-type: none;
        align-items: center;
        text-align: center;
    }
    .buyNow_card_body_desc{
        padding-top: 10px;
       display: list-item;
       list-style-type: none;
    }
    .buyNow_card_body_name{
        color: #333;
        font-size: 18px;
        font-weight: 800;
    }
    .buyNow_card_body_details{
    
    }
    .buyNow_card_body_quantity{
        width: 80%;
        margin-left: auto;
        margin-right: auto;
        margin-top: 5px;
        padding: 5px;
        border-radius: 5px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid #eee;
    }
    .buyNow_card_body_quantity_inc , .buyNow_card_body_quantity_dec{
        font-size: 18px;
        padding: 5px;
        width: 30px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 700;
        background: #eee;
        border-radius: 50%;
        color: #323232;
        margin-left: 20px;
        margin-right: 20px;
        -webkit-user-select: none;
    }
    .buyNow_card_body_quantity_inc:hover , .buyNow_card_body_quantity_dec:hover{
        background: rgb(233, 56, 56);
        color: white;
        cursor: pointer;
    }
    .buyNow_card_body_right{
        width: 50%;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    .buyNow_card_body_price{
        font-weight: 700;
        color: #777;
    }
    .buyNow_card_body_action{
        display: list-item;
        list-style-type: none;
        align-items: stretch;
    }
    .buyNow_card_body_action div{
        margin-bottom: 30px;
        font-size: 14px;
        font-weight: 400;
        cursor: pointer;
    }
    .buyNow_card_body_action div:hover{
        color:  rgb(233, 56, 56);
    }
    .buyNow_card_footer{
        display: flex;
        justify-content: space-between;
        border-top: 2px solid #eee;
    }
    .buyNow_card_footer_left{
        width: 70%;
        padding-top: 10px;
        display: list-item;
        list-style-type: none;
        align-items: center;
    }
    .buyNow_card_footer_left_flex{
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    .buyNow_card_footer_c_input{
       width: 110%;
       padding: 5px ;
       border: 1px solid #cdcdcd;
       border-radius: 5px;
       box-shadow: none;
       outline: none;
    }
    .buyNow_card_footer_c_btn{
        border: 0;
        box-shadow: none;
        outline: none;
        border-radius: 5px;
        background: rgb(233, 56, 56);
        padding: 5px;
        padding-left: 7px;
        padding-right: 7px;
        color: white;
        border: 1px solid  rgb(233, 56, 56);
    }
    .buyNow_card_footer_c_btn:hover{
        color:  rgb(233, 56, 56);
        background: white;
        border: 1px solid  rgb(233, 56, 56);
    }
    .couponDesc{
        /* display: flex;
        justify-content: center;
        align-items: center; */
        color: rgb(8, 211, 52);
        font-size: 12px;
        font-weight: 400;
        padding-top: 5px;
        padding-bottom: 5px;
        text-align: center;
    }
    .buyNow_card_footer_gst_input{
        width: 110%;
        padding: 5px ;
        border: 1px solid #cdcdcd;
        border-radius: 5px;
        box-shadow: none;
        outline: none;
     }
     .buyNow_card_footer_gst_btn{
         border: 0;
         box-shadow: none;
         outline: none;
         border-radius: 5px;
         background: rgb(233, 56, 56);
         padding: 5px;
         padding-left: 30px;
         padding-right: 30px;
         color: white;
         border: 1px solid  rgb(233, 56, 56);
     }
     .buyNow_card_footer_gst_btn:hover{
         color:  rgb(233, 56, 56);
         background: white;
         border: 1px solid  rgb(233, 56, 56);
     }
     .gstinDesc{
        /* display: flex;
        justify-content: center;
        align-items: center; */
        color: rgb(8, 211, 52);
        font-size: 12px;
        font-weight: 400;
        padding-top: 5px;
        padding-bottom: 5px;
        text-align: center;
    }
    .total_price{
        margin: 5px;
        margin-right: 15px;
        margin-left: 20px;
        text-align: end;
    }
    .border_bottom{
        border-bottom: 1px solid #eee;
    }
    .total_price_amt{
        font-size: 17px;
        font-weight: bolder;
        color: #000;
    }
    .combo_Button_container{
        display: flex;
        justify-content: center;    
    }
    .combo_button{
        width: 45%;
        display: flex;
        justify-content: space-around;  
        text-align: center;
        font-size: 15px;
        cursor: pointer;
        color: rgb(32, 124, 230);
    }
    .couponCode_button{
        width: 50%;
        margin: 0;
        background: #eee;
        border: 1px solid #FFF;
        border-top-left-radius: 10px;
        border-bottom-left-radius: 10px;
        transition: 0.5s ease;
    }
    .couponCode_button:hover{
        background: rgb(32, 124, 230);
        color: white;
        transform: translateY(-3px);
    }
    .GSTIN_button{
        width: 50%;
        margin: 0;
        color: rgb(37, 197, 122);;
        background: #eee;
        border: 1px solid #FFF;
        border-top-right-radius: 10px;
        border-bottom-right-radius: 10px;
        transition: 0.5s ease;
    }
    .GSTIN_button:hover{
        background: rgb(23, 226, 131);
        color: white;
        transform: translateY(-3px);        
    }
    .hide div{
        display: none;
    }
    .discountAmount{
        padding: 5px;
    }
    .dispNone{
        display: none!important;
    }
    .payNow, .next{
        display: flex;
        justify-content: flex-end;
        padding-top: 10px;
        padding-bottom: 10px;
    }
    .payNow_btn , .next_btn{
        outline: none;
        padding: 5px;
        padding-left: 20px;
        padding-right: 20px;
        box-shadow: none;
        border: 0;
        color: white;
        margin-right: 30px;
        border-radius: 15px;
        background: linear-gradient( to left , rgb(95, 176, 230) , rgb(4, 107, 190) );
        box-shadow: 2px 0px 3px 0px rgb(80, 169, 221) ;
        transition: 1s ease;
    }
    .payNow_btn:hover , .next_btn:hover { 
        background: linear-gradient( to right , rgb(95, 176, 230) , rgb(4, 107, 190) );
    }
}
@media(max-width:768px){
    .buyNow_header{
        display: flex;
        justify-content: space-between;
    }
    .buyNow_modal{
        display: list-item;
        list-style-type: none;
    }
    .modal_left , .modal_right{
        width: 100%;
    }
    .coursesFrom , .coursesTo{
        width: 90%;
        margin-left: auto;
        margin-right: auto;
        margin-top: 10px;
        margin-bottom: 10px;
    }
    .buyNow_card{
        width: 90%;
        margin-left: auto;
        margin-right: auto;
    }
    .buyNow_card_body_img{
        width: 60%;
        display: none;    
    }
    .buyNow_card_footer{
        display: list-item;
        list-style-type: none;
    }
    .buyNow_card_footer_left{
        width: 100%;
        display: list-item;
        list-style-type: none;
    }
    .buyNow_card_footer_left_flex{
        display: list-item!important;
        list-style-type: none;
    }
    .buyNow_card_footer_c_input{
        width: 70%;
    }
    .buyNow_card_footer_c_btn{
        width: 40%;
    }
    .buyNow_card_footer_gst_input{
        width: 70%;
    }
    .buyNow_card_footer_gst_btn{
        width: 40%;
    }
    .buyNow_card_footer_left div{
        display: flex;
        justify-content: center;
        padding-top: 10px;
    }
    .combo_button{
        width: 90%;
        display: list-item;
        list-style-type: none;
    }
    .couponCode_button{
        width: 100%;
        border-radius: 10px;
    }
    .GSTIN_button{
        width: 100%;
        border-radius: 10px;
    }
    .hide div{
        display: none;
    }
}
