@media(max-width:8000px){
    .add_users_container{
        display:list-item;
        list-style-type: none;
        text-align: center;
    }
    .add_users_back_btn{
        margin-top: 20px;
        display: flex;
        justify-content: flex-start;
        padding-left: 20px;
        align-items: center;
        font-size: 14px;
        font-weight: 500;
        color: rgb(0, 119, 255) ;
    }
    .courseName{
        width: 50%;
        text-align: center;
        color: black;
        font-size: 20px;
        font-weight: 700;
    }
    .add_users_box{
        width: 50%;
        padding: 20px;
        padding-left: 50px;
        display: flex;
        justify-content: space-between; 
        transition: 1s ease;       
    }
    .add_users_back_btn:hover{
        transform: translateY(-3px);
        cursor: pointer;
    }
    .add_emails_container{
        width: 100%;
    }
    .add_emails_container_header{
        width: 100%;
        display: flex;
        justify-content: space-around;
    }
    .add_users_text , .already_registered_text{
        font-size: 16px;
        font-weight: 600;
        color: #666;
    }
    .add_emails_container_body{
        border: 1px solid #eee;
        display:list-item;
        list-style-type: none;
        height: 50vh;
        padding: 10px;
        overflow-y: scroll;
    }
    .email_input_box{
        margin-top: 10px;
        margin-left: auto;
        margin-right: auto;
        width: 90%;
        border: 1px solid #ddd;
        display: flex;
        padding: 10px;
        justify-content: space-between;
        align-items: center;
    }
    .user_email_input{
        width: 150%;
        outline: none;
        box-shadow: none;
        border: 1px solid #ddd;
        padding: 5px;
    }
    .already_registered_checkbox{
        margin-right: 100px;
    }
    .add_email_btn_container{
        margin-top: 10px;
        width: 75%;
        display: flex;
        justify-content: center;
    }
    .add_email_btn{    
        font-size: 14px;
        font-weight: 400;
        border-radius: 10px;
        margin-left: 20px;
        margin-right: 20px;
        padding: 5px;
        padding-left: 20px;
        padding-right: 20px;
        -webkit-user-select: none;
        color: rgb(233, 56, 56);
        border: 1px solid rgb(233, 56, 56);;;
    }
    .add_email_btn:hover{
        background: rgb(233, 56, 56);
        color: white;
        cursor: pointer;
    }
    .quantity_alert{
        width: 50%;
        margin-top: 10px;
        color: rgb(255, 51, 0);
        text-align: center;
        margin-bottom: 100px;
    }
    .add_users_submit{
        width: 100%;
        position: absolute;
        bottom: 0;
        display: flex;
        justify-content: center;
    }
    .add_users_submit_btn{
        outline: none;
        padding: 5px;
        padding-left: 20px;
        padding-right: 20px;
        margin: 20px;
        box-shadow: none;
        border: 0;
        color: white;
        margin-right: 30px;
        border-radius: 15px;
        background: linear-gradient( to left , rgb(95, 176, 230) , rgb(4, 107, 190) );
        box-shadow: 2px 0px 3px 0px rgb(80, 169, 221) ;
        transition: 1s ease;
    }
    .add_users_submit_btn:hover{
        background: linear-gradient( to right , rgb(95, 176, 230) , rgb(4, 107, 190) );
    }
}
@media(max-width:768px){
    .add_users_box{
        width: 90%;        
        display: flex;
        justify-content: space-between;
    }
    .courseName{
        width: 100%;
        margin-top: 10px;
        text-align: center;
    }
    .add_users_box{
        width: 100%;
        padding: 0;
    }
    .add_emails_container{
        width: 100%;
    }
    .add_emails_container_header{
        width: 100%;
    }
    .add_emails_container_body{
        height: 60vh;
    }
    .email_input_box{
        display: flex;
        justify-content: space-between;
    }
    .user_email_input{
        width: 170%;
    }
    .already_registered_checkbox{
        width: 50px;
        margin: 0;
    }
    .add_email_btn_container{
        width: 100%;
        display: flex;
        justify-content: center;
    }    
    .quantity_alert{
        width: 100%;
    }
}