.App {
  background: #FFF;
  /* font-family: 'Poppins', sans-serif; */
  height: 100vh;
  width: 100%;
  overflow-x: hidden;
}
::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: rgba(0,0,0,0); 
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #eee;
}
